.recipe{
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, rgba(0, 0, 0, 0.12) 0px -12px 30px, rgba(0, 0, 0, 0.12) 0px 4px 6px, rgba(0, 0, 0, 0.17) 0px 12px 13px, rgba(0, 0, 0, 0.09) 0px -3px 5px;
    margin: 25px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background-color: white;
    align-items: center;
    min-width: 40%;
    min-height: 50%;
    
}
.rr{
    max-width: 500px;
    font-family: 'Courier New', Courier, monospace;
    font-size: 15px;
    color: teal;
    font-weight: 700;
    margin: 10px;
}
.b1{
    display: flex;
    font-size: 45px;
    color: #355E3B;
    margin: 10px;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}
.b2{
    
    font-size: 25px;
    color:	#49796B;
   
    margin: 10px;
    max-height: 20px;
}

.b3{
    
    font-size: 25px;
    color:#49796B;
   
    margin: 10px;
    max-height: 20px;
}
.b4{
    
    font-size: 25px;
    color:#49796B;
    font-family: 'Courier New', Courier, monospace;
    margin: 10px;
    max-height: 20px;
    font-weight: 600;
}
.img1{
    max-height: 250px;
    max-width: 250px;
    margin: 20px;
    border-radius: 50%;
    border: 5px solid #49796B;
    display:block;
    
}
.b7{
    display: flex;
