.App {
  text-align: center;
  min-height: 100vh;
  background-image: linear-gradient(-225deg, #DFFFCD 0%, #90F9C4 48%, #39F3BB 100%);
}

.search-form{
  min-height: 10vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-bar {
  width: 40%;
  border: none;
  padding: 10px;
  border: 2px solid;
  border-color: #355E3B;
  color: #355E3B;
  font-weight: 500;
  outline: none;
}

.search-button{
background:#355E3B;
border: none;
padding: 10px 20px;
color: #fff;
font-weight: 600;
cursor: pointer;
transition:0.5s;
border: 2px solid;
  border-color: #355E3B;
  outline: none;
}
.search-button:hover{
  background: cadetblue;
 border-color: cadetblue;
  
 
  
}

.recipes {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.a{
  font-size: 75px;
  color: #29AB87;
  margin: 10px;
  margin-top: 0px;

}
.s{
  font-size: 25px;
  font-family: 'Courier New', Courier, monospace;
  color:cadetblue;
  margin: 10px;
}